import React, { Component } from 'react';
import StatList from "./StatList";
import style from "./Feedback.module.css";

class Feedback extends Component {

  state = {
        good: 0,
        neutral: 0,
        bad: 0,
        total: 0,
        goodParcent: 0,
    }
 
//нужно как-то объединить это все....  
  
countFeedback = (event) => {
    if (event.target.name === "good") {
      this.setState(prevState => ({
        [event.target.name]: prevState.good + 1,
      }));
      this.countTotalFeedback();
      this.countPositiveFeedbackPercentage();
      
    }

    if (event.target.name === "neutral") {
      this.setState(prevState => ({
        [event.target.name]: prevState.neutral + 1,
      }));
      this.countTotalFeedback();
      this.countPositiveFeedbackPercentage();
    }

    if (event.target.name === "bad") {
      this.setState(prevState => ({
        [event.target.name]: prevState.bad + 1,
      }));
      this.countTotalFeedback();
      this.countPositiveFeedbackPercentage();
    }

  }
 //.....
  
  countTotalFeedback() {
    this.setState(prevState => ({ total: prevState.total + 1 }));
    
}
        
  countPositiveFeedbackPercentage() {
    this.setState(prevState => ({goodParcent: Math.round(prevState.good / prevState.total * 100)}))      
  }
  

  render() {
    return (
    <div>
      <h2 className="stat__title">Пожалуйста, оставьте отзыв</h2>

        <button className={style.stat__button} name="good" type="button" onClick={this.countFeedback}>Хорошо</button>
        <button className={style.stat__button} name="neutral" type="button" onClick={this.countFeedback}>Нейтрально</button>
        <button className={style.stat__button} name="bad" type="button" onClick={this.countFeedback}>Плохо</button>
        <h2 className="stat__title">Статистика</h2>
        {this.state.total > 0
          ? <StatList
          neutral  = {this.state.neutral}
          bad = {this.state.bad}
          total = {this.state.total}
          goodParcent = {this.state.goodParcent}
          good = {this.state.good}

          />
          : <p>Еще нет голосов</p> 
        }

    </div>
  )
  }
}

export default Feedback;